/*动画*/
.fade-enter-active[data-v-3de8bcdc],
.fade-leave-active[data-v-3de8bcdc] {
    -webkit-transition: all .2s linear;
    transition: all .2s linear;
    -webkit-transform: translate3D(0, 0, 100%);
            transform: translate3D(0, 0, 100%);
}
.fade-enter[data-v-3de8bcdc],
.fade-leave-active[data-v-3de8bcdc] {
    -webkit-transform: translate3D(0,100%, 0);
            transform: translate3D(0,100%, 0);
}
/* bigimg */
.img-view[data-v-3de8bcdc] {
    position: fixed;
    width: 100%;
    height: 100%;
    top:25%;
}
/*遮罩层样式*/
.img-view .img-layer[data-v-3de8bcdc] {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
}
/*不限制图片大小，实现居中*/
.img-view .img img[data-v-3de8bcdc] {
    max-width: 100%;
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1000;
}
